.s-cms-content {
    font-size: 16px;
    line-height: 1.5;
    margin-bottom: 24px;
    @include mq($from: desktop) {
        margin-bottom: 40px;
    }

    &:last-child {
        margin-bottom: 0;
    }

    p {
        margin: 20px 0 0;

        &:first-child {
            margin-top: 0;
        }
    }     

    &--center {
        text-align: center;
    }

    &--small-width {
        max-width: 700px;
        margin-left: auto;
        margin-right: auto;
    }

    strong {
        font-weight: 700;
    }

    a:not([class]) {
        text-decoration: underline;
        font-weight: 700;
        transition: color 0.3s ease;

        &:hover {
            transition: color 0.3s ease;
        }
    }

    img, video, figure {
        max-width: 570px;
        height: auto;
        display: block;
        margin: 20px auto;
        width: 100%;
        box-shadow: $global-box-shadow;

        &.native {
            max-width: 100%;
        }
    }

    figure > img {
        width: 100%;
        height: auto;
        display: block;
        margin: 0;
        box-shadow: none;
    }

    ul:not([class]) {
        padding-left: 20px;
        margin: 10px 0 20px;
        &:first-child {
            margin-top: 0;
        }
        &:last-child {
            margin-bottom: 0;
        }
        @include mq($from: desktop) {
            margin: 30px 0;
        }

        li {
            margin-bottom: 15px;
            &:last-child {
                margin-bottom: 0;
            }
            @include mq($from: desktop) {
                margin-bottom: 20px;
            }
            
            &::marker {
                color: $color-blue;
            }
        }
    }

    ol:not([class]) {
        padding-left: 20px;
        margin: 10px 0 20px;
        &:first-child {
            margin-top: 0;
        }
        @include mq($from: desktop) {
            margin: 30px 0;
        }

        li {
            margin-bottom: 15px;
            @include mq($from: desktop) {
                margin-bottom: 20px;
            }
            
            &::marker {
                font-weight: 600;
            }
        }
    }
}