.s-cms-content {
  font-size: 16px;
  line-height: 1.5;
  margin-bottom: 24px;
}
@media (min-width: 61.25em) {
  .s-cms-content {
    margin-bottom: 40px;
  }
}
.s-cms-content:last-child {
  margin-bottom: 0;
}
.s-cms-content p {
  margin: 20px 0 0;
}
.s-cms-content p:first-child {
  margin-top: 0;
}
.s-cms-content--center {
  text-align: center;
}
.s-cms-content--small-width {
  max-width: 700px;
  margin-left: auto;
  margin-right: auto;
}
.s-cms-content strong {
  font-weight: 700;
}
.s-cms-content a:not([class]) {
  text-decoration: underline;
  font-weight: 700;
  transition: color 0.3s ease;
}
.s-cms-content a:not([class]):hover {
  transition: color 0.3s ease;
}
.s-cms-content img, .s-cms-content video, .s-cms-content figure {
  max-width: 570px;
  height: auto;
  display: block;
  margin: 20px auto;
  width: 100%;
  box-shadow: 0px 6px 30px rgba(0, 0, 0, 0.08);
}
.s-cms-content img.native, .s-cms-content video.native, .s-cms-content figure.native {
  max-width: 100%;
}
.s-cms-content figure > img {
  width: 100%;
  height: auto;
  display: block;
  margin: 0;
  box-shadow: none;
}
.s-cms-content ul:not([class]) {
  padding-left: 20px;
  margin: 10px 0 20px;
}
.s-cms-content ul:not([class]):first-child {
  margin-top: 0;
}
.s-cms-content ul:not([class]):last-child {
  margin-bottom: 0;
}
@media (min-width: 61.25em) {
  .s-cms-content ul:not([class]) {
    margin: 30px 0;
  }
}
.s-cms-content ul:not([class]) li {
  margin-bottom: 15px;
}
.s-cms-content ul:not([class]) li:last-child {
  margin-bottom: 0;
}
@media (min-width: 61.25em) {
  .s-cms-content ul:not([class]) li {
    margin-bottom: 20px;
  }
}
.s-cms-content ul:not([class]) li::marker {
  color: #0057A7;
}
.s-cms-content ol:not([class]) {
  padding-left: 20px;
  margin: 10px 0 20px;
}
.s-cms-content ol:not([class]):first-child {
  margin-top: 0;
}
@media (min-width: 61.25em) {
  .s-cms-content ol:not([class]) {
    margin: 30px 0;
  }
}
.s-cms-content ol:not([class]) li {
  margin-bottom: 15px;
}
@media (min-width: 61.25em) {
  .s-cms-content ol:not([class]) li {
    margin-bottom: 20px;
  }
}
.s-cms-content ol:not([class]) li::marker {
  font-weight: 600;
}

